import React,{useEffect} from 'react'
import styled from 'styled-components'
import { auth, provider } from '../firebase'
import {useSelector,useDispatch} from 'react-redux';
import {useHistory} from 'react-router-dom';
import {selectUserName,selectUserEmail,selectUserPhoto,setUserLoginDetails, setSignOutState} from '../feattures/user/userSlice';
import {Link} from 'react-router-dom';


export default function Header() {
    const dispatch = useDispatch();
    
    const history = useHistory();

    const username = useSelector(selectUserName);
    const userphoto = useSelector(selectUserPhoto);
    
    useEffect(() => {
        auth.onAuthStateChanged(async (user)=>{
            if(user){
                setUser(user);
                history.push('/home');
            }
        })
    }, [username])

    const handeelAuth = ()=>{
       if(!username){
        auth.signInWithPopup(provider).then((result)=>{
            setUser(result.user);
        })
        .catch((err)=>{
            alert(err.message)
        })
       }else if(username){
           auth.signOut()
           .then(()=>{
               dispatch(setSignOutState());
               history.push('/');
           })
           .catch(err => {
               console.log(err);
           })
       }
    }

    const setUser = (user)=>{
        dispatch(setUserLoginDetails({
            name:user.displayName,
            email:user.email,
            photo:user.photoURL
        }));
        
    }
    return (
        <Nav>
            <Logo>
                <img src="/images/logo.svg" alt="Disney+" />
            </Logo>
            {
                !username ? <Login onClick={handeelAuth}>Login</Login> 
                : (
                    <>
                    <Navmenu>
                        <Link to="/home"> <img src="/images/home-icon.svg" alt="home"/><span>Home</span></Link>
                        <Link to="/search"> <img src="/images/search-icon.svg" alt="home"/><span>Search</span></Link>
                        <Link to="/wahchlist"> <img src="/images/watchlist-icon.svg" alt="home"/><span>Watchlist</span></Link>
                        <Link to="/originals"> <img src="/images/home-icon.svg" alt="home"/><span>Originals</span></Link>
                        <Link to="/movies"> <img src="/images/home-icon.svg" alt="home"/><span>Movies</span></Link>
                        <Link to="/series"> <img src="/images/home-icon.svg" alt="home"/><span>Series</span></Link>
                    </Navmenu>
                    <SignOut>
                         <Userimg src={userphoto} alt={username}></Userimg>
                         <DropDown>
                             <span onClick={handeelAuth}>Sign out</span>
                         </DropDown>
                    </SignOut>
                    </>
                )
            }
           
        
        </Nav>
    )
}

const Nav = styled.nav `
    position:fixed;
    top:0;
    left:0;
    right:0;
    height:70px;    
    background-color: #090b13;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0 36px;
    letter-spacing:16px;
    z-index:3;
`;

const Logo = styled.a `
    padding:0;
    width:90px;
    margin-top:2px;
    max-height: 80px;
    font-size: 0;
    display: inline-block;
    img{        
        display: block;
        width: 100%;
    }
`;

const Navmenu = styled.div `
    align-items: center;
    display: flex;
    ${'' /* flex-flow: row-reverse; */}
    justify-content: flex-end;
    margin: 0px;
    padding: 0px;
    position: relative;
    margin-right: auto;
    margin-left: 25px;  
    a{
        display: flex;
        align-items: center;
        padding: 0 15px; 
        img{
            height: 20px;
            min-width: 20px;
            width: 20px;
            z-index: auto;          
        }
        span{            
            color: rgb(249, 249, 249);
            font-size: 13px;
            letter-spacing: 1.42px;
            line-height: .68px;
            padding:2px 0px;
            font-size: 15px;
            white-space: nowrap;
            position: relative;      
            &:before{
                background-color: rgb(249, 249, 249);
                border-radius: 0 0 4px 4px;
                bottom: -10px;
                display: block;
                content:"";
                height: 2px;
                left:0px;
                opacity: 0;
                position: absolute;
                right: 0px;
                transform-origin: left center;
                transform: scaleX(0);
                transition: all 250 cubic-bezier(0.25,0.46,0.45);
                visibility: hidden;
                width: auto;                      
            }
        }
        &:hover{
            span:before{
                transform: scaleX(1);
                visibility: visible;
                opacity: 1 !important;
            }
        }
    }
    ${'' /* @media (max-width:768px) {
        display: none;
    }   */}
`;

const Login = styled.a `
    background-color: rgb(0, 0, 0.6);
    padding: 8px 16px;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    border: 1px solid #f9f9f9;
    border-radius: 4px; 
    transition: all 0.2 ease 0s;
    cursor:pointer;
    &:hover{        
        background-color: #f9f9f9;
        color: black;
        border-color:transparent;
    }  
`;

const Userimg = styled.img `
    height: 65%;
    border-radius:50%;
    cursor:pointer;
`;

const DropDown = styled.div `
    position: absolute;
    top: 48px;
    right: 0px;
    background: rgb(19, 19, 19);
    border: 1px solid rgba(151, 151, 151, 0.34);
    border-radius: 4px;
    box-shadow: rgb(0 0 0 / 50%) 0px 0px 18px 0px;
    padding: 10px;
    font-size: 14px;
    letter-spacing: 3px;
    width: 100px;
    opacity: 0;
`;

const SignOut = styled.div `
  position: relative;
  height: 48px;
  width: 48px;
  display: flex;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  ${Userimg} {
    border-radius: 50%;
    width: 100%;
    height: 100%;
  }
  &:hover {
    ${DropDown} {
      opacity: 1;
      transition-duration: 1s;
    }
  }
`;

